<template>
    <div id="Circum">
        <el-collapse v-model="activeNames">
            <el-collapse-item title="" name="1">
                <div class="swichcontent">
                    <span>:</span>
                    <el-select popper-class="popper" v-model="crSt.scenemode" placeholder="">
                        <el-option label="" value="1"/>
                        <el-option label="" value="2"/>
                        <el-option label="" value="3"/>
                    </el-select>
                </div>


            </el-collapse-item>
            <el-collapse-item title="" name="2">
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.showSun"></el-switch>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.showMoon"></el-switch>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-select popper-class="popper" v-model="crSt.backmode" placeholder="">
                        <el-option label="" value="1"/>
                        <el-option label="" value="2"/>
                    </el-select>
                </div>
                <div class="swichcontent" v-if="crSt.backmode==1">
                    <span>:</span>
                    <el-select popper-class="popper" v-model="crSt.backmodepars.skyboxnum" placeholder="">
                        <el-option label="" value="1"/>
                        <el-option label="" value="2"/>
                        <el-option label="" value="3"/>
                        <el-option label="" value="4"/>
                        <el-option label="" value="5"/>
                        <el-option label="" value="6"/>
                        <el-option label="" value="7"/>
                        <el-option label="" value="8"/>
                        <el-option label="" value="9"/>
                    </el-select>
                </div>
                <div class="swichcontent" v-if="crSt.backmode==1">
                    <span>:</span>
                    <el-switch v-model="crSt.backmodepars.skyboxrotate" />
                </div>
                <div class="swichcontent" v-if="crSt.backmode==1">
                    <span>:</span>
                    <el-slider v-model="crSt.backmodepars.skyboxrs" :min="-10" :max="10" :step="0.01"/>
                </div>

                <div class="swichcontent" v-if="crSt.backmode==2">
                    <span>:</span>
                    <el-color-picker v-model="crSt.backmodepars.colorcss" />
                </div>


            </el-collapse-item>
            <el-collapse-item title="" name="3">
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.outatm"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.fog"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.earthatm"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.highdyren"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-color-picker v-model="crSt.globalcol" />
                </div>

            </el-collapse-item>
            <el-collapse-item title="" name="4">
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.fixedlight"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.shadow"/>
                </div>
                <div class="swichcontent" v-if="crSt.shadow">
                    <span>:</span>
                    <el-switch v-model="crSt.terrainsh"/>
                </div>
                <div class="swichcontent" v-if="!crSt.shadow">
                    <span>:</span>
                    <el-switch v-model="crSt.showdaynight"/>
                </div>
            </el-collapse-item>
            <el-collapse-item title="" name="5">
                <!--<div class="swichcontent">-->
                    <!--<span>:</span>-->
                    <!--<el-switch v-model="crSt.exciplityrend"/>-->
                <!--</div>-->
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.fxaa"/>
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-switch v-model="crSt.depthtest"/>
                </div>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script setup>
    import {circumsettings} from "./buss/circum";
    const crSt=circumsettings();
    import {ref,onMounted,watch} from 'vue'
	const activeNames = ref(['1', '2', '3', '4','5']);

	watch(() => crSt.backmode, (newval, oldval) => {
		crSt.SetBackModes();
	}, {deep: true});
	watch(() => crSt.backmodepars.colorcss, (newval, oldval) => {
		crSt.SetBackModes();
	}, {deep: true});
	watch(() => crSt.backmodepars.skyboxnum, (newval, oldval) => {
		crSt.SetBackModes();
	}, {deep: true});

	watch(() => crSt.backmodepars.skyboxrotate, (newval, oldval) => {
		crSt.SetBackModes();
	}, {deep: true});
	watch(() => crSt.backmodepars.skyboxrs, (newval, oldval) => {
		crSt.SetBackModes();
	}, {deep: true});

	watch(() => crSt.showSun, (newval, oldval) => {
		crSt.SetSunShow();
	}, {deep: true});
	watch(() => crSt.showMoon, (newval, oldval) => {
		crSt.SetMoonShow();
	}, {deep: true});
	watch(() => crSt.scenemode, (newval, oldval) => {
		crSt.SetSceneMode();
	}, {deep: true});
	watch(() => crSt.outatm, (newval, oldval) => {
		crSt.SetOutAtmos();
	}, {deep: true});
	watch(() => crSt.fog, (newval, oldval) => {
		crSt.SetFog();
	}, {deep: true});
	watch(() => crSt.earthatm, (newval, oldval) => {
		crSt.SetGoundAtmos();
	}, {deep: true});
	watch(() => crSt.highdyren, (newval, oldval) => {
		crSt.SetHdr();
	}, {deep: true});
	watch(() => crSt.globalcol, (newval, oldval) => {
		crSt.SetGlobeColor();
	}, {deep: true});
	watch(() => crSt.fixedlight, (newval, oldval) => {
		crSt.SetFixLight();
	}, {deep: true});
	watch(() => crSt.shadow, (newval, oldval) => {
		crSt.SetShadow();
	}, {deep: true});
	watch(() => crSt.terrainsh, (newval, oldval) => {
		crSt.SetTerrainShadow();
	}, {deep: true});
	watch(() => crSt.showdaynight, (newval, oldval) => {
		crSt.SetDayNight();
	}, {deep: true});
	watch(() => crSt.showdaynight, (newval, oldval) => {
		crSt.SetFxaa();
	}, {deep: true});
	watch(() => crSt.depthtest, (newval, oldval) => {
		crSt.SetDepthTest();
	}, {deep: true});

</script>

