import {defineStore} from "pinia";
import {GlobalState} from "../../../buss/maphandler";
import {circumsettings} from "./circum";
import {othersettings} from "./widgets";

export const clocksettings=defineStore('clocksettings',{
	state:()=>{
		return {
			starttime:null,
			endtime:null,
			timemode:"1",
			curtime:null,
			multiply:1
		}
	},
	actions: {
		SetStartTime() {
			if(this.starttime)	{
				GlobalState.viewer.clock.startTime=Cesium.JulianDate.fromDate(new Date(this.starttime));
			}
		},
		SetEndTime() {
			if(this.endtime) GlobalState.viewer.clock.stopTime=Cesium.JulianDate.fromDate(new Date(this.endtime));
		},
		SetTimeMode(){
			switch (this.timemode) {
				case "1":
					GlobalState.viewer.clock.clockRange=Cesium.ClockRange.UNBOUNDED;
					break;
				case "2":
					GlobalState.viewer.clock.clockRange=Cesium.ClockRange.CLAMPED;
					break;
				case "3":
					GlobalState.viewer.clock.clockRange=Cesium.ClockRange.LOOP_STOP;
					break;
				default:
					break;
			}
		},
		SetCurTime(){
			if(this.curtime) {
				GlobalState.viewer.clock.currentTime=Cesium.JulianDate.fromDate(new Date(this.curtime));
				othersettings().timecontrolstrs.currenttime=this.curtime;
			}
		},
		SetMultiply()
		{
			GlobalState.viewer.clock.multiplier=this.multiply;
			othersettings().timecontrolstrs.step=this.multiply;
		}
	}

})

export function InitClockSt(settings) {
	if (settings) {
		for (let key in settings) {
			clocksettings()[key] =settings[key];
		}
	}

	clocksettings().SetStartTime();
	clocksettings().SetEndTime();
	clocksettings().SetTimeMode();
	clocksettings().SetCurTime();
	clocksettings().SetMultiply();

}

export function GetClockSt() {
	const result={};
	for (let key in clocksettings().$state) {
		result[key] =clocksettings()[key];
	}
	return result;
}
