import {defineStore} from "pinia";
import {GlobalState} from "../../../buss/maphandler";


import GroundSkyBox from "@/modules/cesium/common/buss/GroundSkyBox.js";


let sunsetting = {
	_sunPositionWC: new Cesium.Cartesian3(),
	_sunDirectionWC: new Cesium.Cartesian3(),
	_sunPositionColumbusView: new Cesium.Cartesian3()
};
export const circumsettings=defineStore('circumsettings',{
	state: () => {
		return {
			scenemode:"1",
			showSun:true,
			showMoon:true,
			backmode: '1',
			backmodepars: {
				skyboxnum:"1",
				skyboxrotate:false,
				skyboxrs:0,
				colorcss:'#5B86AA',
			},

			outatm:true,
			fog:true,
			earthatm:true,
			highdyren:false,
			globalcol:'#0D4DB5',

			fixedlight:false,
			shadow:false,
			terrainsh:false,
			showdaynight:false,

			// exciplityrend:true,
			fxaa:true,
			depthtest:true,
		}
	},
	actions:{
		SetBackModes(){
			//statenewval
			switch (this.backmode) {
				case "1":
					GlobalState.viewer.scene.skyBox.show=true;
					GlobalState.viewer.scene.skyBox = new GroundSkyBox({
						sources: {
							negativeX: `/img/skybox/${this.backmodepars.skyboxnum}/nx.png`,
							positiveX: `/img/skybox/${this.backmodepars.skyboxnum}/px.png`,
							negativeY: `/img/skybox/${this.backmodepars.skyboxnum}/ny.png`,
							positiveY: `/img/skybox/${this.backmodepars.skyboxnum}/py.png`,
							negativeZ: `/img/skybox/${this.backmodepars.skyboxnum}/nz.png`,
							positiveZ: `/img/skybox/${this.backmodepars.skyboxnum}/pz.png`,
						},
						rotate: this.backmodepars.skyboxrotate,
						speed: this.backmodepars.skyboxrs,
					});
					break;
				case "2":
					GlobalState.viewer.scene.skyBox.show=false;
					GlobalState.viewer.scene.backgroundColor=Cesium.Color.fromCssColorString(this.backmodepars.colorcss);
					break;
				default:
					break;
			}

		},
		SetSunShow(){
			GlobalState.viewer.scene.sun.show=this.showSun;

		},
		SetMoonShow(){
			GlobalState.viewer.scene.moon.show=this.showMoon;
		},

		SetSceneMode(){
			switch (this.scenemode) {
				case "1":
					GlobalState.viewer.scene.mode=Cesium.SceneMode.SCENE3D;
					break;
				case "2":
					GlobalState.viewer.scene.mode=Cesium.SceneMode.SCENE2D;
					break;
				case "3":
					GlobalState.viewer.scene.mode=Cesium.SceneMode.COLUMBUS_VIEW;
					break;
				default:
					break;
			}
		},

		SetOutAtmos(){
			GlobalState.viewer.scene.skyAtmosphere.show=this.outatm;
		},
		SetGoundAtmos(){
			GlobalState.viewer.scene.globe.showGroundAtmosphere=this.earthatm;
		},
		SetFog(){
			GlobalState.viewer.scene.fog.enabled=this.fog;
		},
		SetHdr(){
			GlobalState.viewer.scene.highDynamicRange=this.highdyren;
		},
		SetGlobeColor(){
			GlobalState.viewer.scene.globe.baseColor=Cesium.Color.fromCssColorString(this.globalcol);
		},

		SetFixLight(){
			if(this.fixedlight===true){
				setSunAndMoonDirections(
					sunsetting, Cesium.JulianDate.fromDate(new Date()),
					GlobalState.viewer.scene._frameState.mapProjection);

				GlobalState.viewer.scene.light=new Cesium.DirectionalLight({
					direction:sunsetting._sunDirectionWC
				});
			}
			else{
				setSunAndMoonDirections(
					sunsetting, GlobalState.viewer.clock.currentTime,
					GlobalState.viewer.scene._frameState.mapProjection);
				GlobalState.viewer.scene.light=new Cesium.SunLight();
			}
		},
		SetShadow(){
			GlobalState.viewer.shadows = this.shadow;
		},
		SetTerrainShadow(){
			GlobalState.viewer.terrainShadows= (this.terrainsh==true?Cesium.ShadowMode.ENABLED:Cesium.ShadowMode.DISABLED);
		},
		SetDayNight(){
			GlobalState.viewer.scene.globe.enableLighting=this.showdaynight;
		},
		SetFxaa(){
			GlobalState.viewer.scene.postProcessStages.fxaa.enabled = this.fxaa;
		},
		SetDepthTest(){
			GlobalState.viewer.scene.globe.depthTestAgainstTerrain = this.depthtest;
		}




	},


})


export function InitCircumSt(settings) {
	if (settings) {
		for (let key in settings) {
			circumsettings()[key] =settings[key];
		}
	}
	circumsettings().SetBackModes();
	circumsettings().SetSunShow();
	circumsettings().SetMoonShow();
	circumsettings().SetSceneMode();
	circumsettings().SetOutAtmos();
	circumsettings().SetGoundAtmos();
	circumsettings().SetFog();
	circumsettings().SetHdr();
	circumsettings().SetGlobeColor();
	circumsettings().SetFixLight();
	circumsettings().SetShadow();
	circumsettings().SetTerrainShadow();
	circumsettings().SetDayNight();
	circumsettings().SetFxaa();
	circumsettings().SetDepthTest();
}

export function GetCircumSt() {
	const result={};
	for (let key in circumsettings().$state) {
		result[key] =circumsettings()[key];
	}
	return result;
}

let transformMatrix = new Cesium.Matrix3();
const sunCartographicScratch = new Cesium.Cartographic();

function setSunAndMoonDirections(result, time, mapProjection) {

	if (
		!Cesium.defined(
			Cesium.Transforms.computeIcrfToFixedMatrix(time, transformMatrix)
		)
	) {
		transformMatrix = Cesium.Transforms.computeTemeToPseudoFixedMatrix(
			time,
			transformMatrix
		);
	}

	let position = Cesium.Simon1994PlanetaryPositions.computeSunPositionInEarthInertialFrame(
		time,
		result._sunPositionWC
	);
	Cesium.Matrix3.multiplyByVector(transformMatrix, position, position);

	Cesium.Cartesian3.normalize(position, result._sunDirectionWC);


	// position = Cesium.Simon1994PlanetaryPositions.computeMoonPositionInEarthInertialFrame(
	//     time,
	//     uniformState._moonDirectionEC
	// );
	// Matrix3.multiplyByVector(transformMatrix, position, position);
	// Matrix3.multiplyByVector(uniformState.viewRotation3D, position, position);
	// Cartesian3.normalize(position, position);

	const projection = mapProjection;
	const ellipsoid = projection.ellipsoid;
	const sunCartographic = ellipsoid.cartesianToCartographic(
		result._sunPositionWC,
		sunCartographicScratch
	);
	projection.project(sunCartographic, result._sunPositionColumbusView);
}

