import {InitBtns as WidgetInit,GetSettings as WidgetGetSettings, othersettings} from "../components/Widgets/buss/widgets";
import {InitSouceSt as SouceInit} from "../components/Widgets/buss/Source";
import {InitCircumSt as CircumInit, GetCircumSt as CircumGetSettings} from "../components/Widgets/buss/circum";
import {InitClockSt as ClockInit, GetClockSt as ClockGetSettings,clocksettings } from "../components/Widgets/buss/clock";
import {InitCameraSt as CameraInit, GetCameraSt as CameraGetSettings} from "../components/Widgets/buss/camera";
import {InitTerrainSettigns as TerrainInit,GetTerrainSettings as TerrainGetSettings} from "../components/Widgets/buss/terrainMap";
import {InitMaptreeSt as MaptreeInit, GetMaptreeSt as MaptreeGetSettings} from "../components/Widgets/buss/maptree";

import Bus from '@/common/buss/Bus'
export function Init(settings) {
	// if(settings)
	{
		WidgetInit(settings?settings.widgetst:null);
		SouceInit(settings);
		CircumInit(settings?settings.circumst:null);
		ClockInit(settings?settings.clockst:null);
		CameraInit(settings?settings.camerast:null);
		TerrainInit(settings?settings.terrainst:null);
		MaptreeInit(settings?settings.maptreest:null)
	}

	othersettings().timecontrolstrs.currenttime=clocksettings().curtime;
	othersettings().timecontrolstrs.step=clocksettings().multiply;

	Bus.VM.$emit(Bus.SignalType.SCENEBUILDINIT);
}

export function GetTotalSettings() {
	return {
		widgetst:WidgetGetSettings(),
		circumst:CircumGetSettings(),
		clockst:ClockGetSettings(),
		camerast:CameraGetSettings(),
		terrainst:TerrainGetSettings(),
		maptreest:MaptreeGetSettings(),
	}
}



