import {defineStore} from "pinia";
import {GlobalState} from "../../../buss/maphandler";

const colorramps = [
	["#000000", "#2747E0", "#D33B7D", "#D33038", "#FF9742", "#ffd700", "#ffffff"],
];

function getElevationContourMaterial() {
	// Creates a composite material with both elevation shading and contour lines
	return new Cesium.Material({
		fabric: {
			type: "ElevationColorContour",
			materials: {
				contourMaterial: {
					type: "ElevationContour",
				},
				elevationRampMaterial: {
					type: "ElevationRamp",
				},
			},
			components: {
				diffuse:
					"contourMaterial.alpha == 0.0 ? elevationRampMaterial.diffuse : contourMaterial.diffuse",
				alpha:
					"max(contourMaterial.alpha, elevationRampMaterial.alpha)",
			},
		},
		translucent: false,
	});
}

function getSlopeContourMaterial() {
	// Creates a composite material with both slope shading and contour lines
	return new Cesium.Material({
		fabric: {
			type: "SlopeColorContour",
			materials: {
				contourMaterial: {
					type: "ElevationContour",
				},
				slopeRampMaterial: {
					type: "SlopeRamp",
				},
			},
			components: {
				diffuse:
					"contourMaterial.alpha == 0.0 ? slopeRampMaterial.diffuse : contourMaterial.diffuse",
				alpha: "max(contourMaterial.alpha, slopeRampMaterial.alpha)",
			},
		},
		translucent: false,
	});
}

function getAspectContourMaterial() {
	// Creates a composite material with both aspect shading and contour lines
	return new Cesium.Material({
		fabric: {
			type: "AspectColorContour",
			materials: {
				contourMaterial: {
					type: "ElevationContour",
				},
				aspectRampMaterial: {
					type: "AspectRamp",
				},
			},
			components: {
				diffuse:
					"contourMaterial.alpha == 0.0 ? aspectRampMaterial.diffuse : contourMaterial.diffuse",
				alpha: "max(contourMaterial.alpha, aspectRampMaterial.alpha)",
			},
		},
		translucent: false,
	});
}

export const terrainsettings = defineStore('terrainsettings', {
	state: () => {
		return {
			showterrain: false,
			terrainname: '',
			shownetwork: false,
			terrainexa: 1,
			showcontour: false,
			contourstep: 10,
			contourwidth: 1,
			contourcss: '#ff0000',
			groundrender: false,
			rendertype: 1,//1: 2: 3:
			imagestripe: 1,
			minranphei: 0,
			maxranphei: 10000,
			colorramps: [
				["#000000", "#2747E0", "#D33B7D", "#D33038", "#FF9742", "#ffd700", "#ffffff"],
				["#0A5E1C", "#11711E", "#1B8D21", "#2AA827", "#45C436", "#7ADB64", "#A1ED86", "#C9F9B0","#E7FCD7"],
				["#09757A", "#10938D", "#19B79F", "#25DBAD", "#33FFB8", "#66FFBA", "#84FFBC", "#ADFFCA","#D6FFE0"],
				["#0E1478", "#171F92", "#2430B5", "#3544D8", "#495BFC", "#7685FD", "#919EFE", "#B6BFFE"],
				["#7A5400", "#936900", "#B78600", "#DBA600", "#FFC700", "#FFD93F", "#FFE466", "#FFEF99", "#FFF8CC"],
				["#7A0F0F", "#932219", "#B73C27", "#DB5B39", "#FF804F", "#FFA87A", "#FFC195", "#FFDAB8", "#FFEFDB"]
			],
			enableunder:false,
			globea:false,
		}
	},
	actions: {
		SetShowTerrain() {
			if (this.showterrain === false) {
				this.SetTerrain('');
			}
			else {
				this.SetTerrain(this.terrainname);
			}
		},
		SetTerrain(terrainname) {
			const find = GlobalState.viewer.baseLayerPicker._viewModel.terrainProviderViewModels.find(item => item.name == terrainname);
			if (find == null) return;
			GlobalState.viewer.baseLayerPicker._viewModel.selectedTerrain = find;
			this.terrainname = terrainname;
			this.showterrain = (terrainname != '');
		},
		SetShowNetWork() {
			GlobalState.viewer.scene.globe._surface.tileProvider._debug.wireframe = this.shownetwork;
		},
		SetTerrainExa() {
			GlobalState.viewer.scene.globe.terrainExaggeration = this.terrainexa;
		},
		getcolorrampst(item) {
			const duan = item.length - 1;
			const per = 1 / duan;
			const sets = [{color: item[0], per: 0}];
			for (let i = 1; i < item.length - 1; i++) {
				sets.push({color: item[i], per: per * i})
			}
			sets.push({color: item[item.length - 1], per: 1});
			return sets;
		},
		getcolorrampimage(item) {
			const ramp = document.createElement("canvas");
			ramp.width = 100;
			ramp.height = 1;
			const ctx = ramp.getContext("2d");
			let colorsts = this.getcolorrampst(item);
			const grd = ctx.createLinearGradient(0, 0, 100, 0);
			colorsts.forEach(colorst => {
				grd.addColorStop(colorst.per, colorst.color);
			})
			ctx.fillStyle = grd;
			ctx.fillRect(0, 0, 100, 1);
			return ramp;
		},
		SetGlobeMaterial() {
			let material = null;
			let contourUniforms = {};
			let shadingUniforms = {};
			const rendertype = (this.groundrender === true ? this.rendertype : 0);
			if (this.showcontour === true) {
				switch (rendertype) {
					case 0:
						material = Cesium.Material.fromType('ElevationContour');
						contourUniforms = material.uniforms;
						break;
					case 1:
						material = getElevationContourMaterial();
						material.materials.elevationRampMaterial.uniforms.minimumHeight = this.minranphei;
						material.materials.elevationRampMaterial.uniforms.maximumHeight = this.maxranphei;
						contourUniforms = material.materials.contourMaterial.uniforms;
						shadingUniforms = material.materials.elevationRampMaterial.uniforms;
						break;
					case 2:
						material = getSlopeContourMaterial();
						contourUniforms = material.materials.contourMaterial.uniforms;
						shadingUniforms = material.materials.slopeRampMaterial.uniforms;
						break;
					case 3:
						material = getAspectContourMaterial();
						contourUniforms = material.materials.contourMaterial.uniforms;
						shadingUniforms = material.materials.aspectRampMaterial.uniforms;
						break;
					default:
						break;
				}
				contourUniforms.width = this.contourwidth;
				contourUniforms.spacing = this.contourstep;
				contourUniforms.color = Cesium.Color.fromCssColorString(this.contourcss);
				GlobalState.viewer.scene.globe.material = material;
			}
			else {
				switch (rendertype) {
					case 0:
						break;
					case 1:
						material = Cesium.Material.fromType("ElevationRamp");
						material.uniforms.minimumHeight = this.minranphei;
						material.uniforms.maximumHeight = this.maxranphei;
						shadingUniforms = material.uniforms;
						break;
					case 2:
						material = Cesium.Material.fromType("SlopeRamp");
						shadingUniforms = material.uniforms;
						break;
					case 3:
						material = Cesium.Material.fromType("AspectRamp");
						shadingUniforms = material.uniforms;
						break;
					default:
						break;
				}
			}
			if (this.imagestripe != null && this.imagestripe > 0 && this.groundrender === true) {
				shadingUniforms.image = this.getcolorrampimage(this.colorramps[this.imagestripe - 1]);
			}
			GlobalState.viewer.scene.globe.material = material;
		},
		SetGroundA(){
			GlobalState.viewer.scene.globe.translucency.enabled = this.globea;
			GlobalState.viewer.scene.globe.translucency.frontFaceAlpha = this.globea===true?0.2:1;
		},
		SetEnableUnder(){
			GlobalState.viewer.scene.screenSpaceCameraController.enableCollisionDetection = this.enableunder;
		}
	}
});

export function InitTerrainSettigns(settings) {
	if (settings) {
		for (let key in settings) {
			terrainsettings()[key] = settings[key];
		}
	}
	terrainsettings().SetShowTerrain();
	terrainsettings().SetShowNetWork();
	terrainsettings().SetTerrainExa();
	terrainsettings().SetGlobeMaterial();
	terrainsettings().SetGroundA();
	terrainsettings().SetEnableUnder();
}

export function GetTerrainSettings() {
	const result = {};
	for (let key in terrainsettings().$state) {
		if (key != 'colorramps') {
			result[key] = terrainsettings()[key];
		}
	}
	return result;
}
