import {CesiumViewer, GetCamera, GetPickedRayPositionWGS84,GlobalState} from '../../../buss/maphandler';
import {defineStore} from "pinia";
import Bus from '@/common/buss/Bus'
import moment from 'moment';
//
const tooltipbtns = [
	{key: 'home'},
	{key: 'compass'},
	{key: 'zoominout'},
	{key: 'scenemode'},
	{key: 'imapk'},
	{key: 'fullsize'},
	{key: 'navi'},
];
export const othersettings = defineStore('othersettings', {
		state: () => {
			return {
				//
				statusbar: true,
				lonlat: true,
				height: true,
				lod: true,
				heading: true,
				pitch: true,
				roll: true,
				cameraheight: true,
				fps: true,
				lonlatpre: 6,
				//
				scale: true,
				scaleleft: '80px',
				scaleright: '',
				scaletop: '',
				scalebottom: '30px',
				//
				timeline: true,
				animation: true,
				//
				statusbarstrs: {
					lon: '', lat: '', height: '', lod: '', heading: '', pitch: '', roll: '', cameraheight: '', fps: ''
				},
				//
				scalebarstr: '',
				//
				timecontrolstrs: {
					currenttime:null,
					shouldanimate: true,
					timeformat: 'YYYY-MM-DD HH:mm:ss',
					step:1,

				}
			}
		},
		getters: {
			statuscontents(state) {
				let result = '';
				if (state.lonlat === true) {
					result += `Longitude${state.statusbarstrs.lon}   Latitude${state.statusbarstrs.lat} `;
				}
				if (state.height === true) {
					result += `Height${state.statusbarstrs.height}m`;
				}
				if (state.lod === true) {
					result += `Level${state.statusbarstrs.lod}   `;
				}
				if (state.heading === true) {
					result += `Heading${state.statusbarstrs.heading}   `;
				}
				if (state.pitch === true) {
					result += `Pitch${state.statusbarstrs.pitch}   `;
				}
				if (state.roll === true) {
					result += `Roll${state.statusbarstrs.roll}   `;
				}
				if (state.cameraheight === true) {
					result += `CameraHeight${state.statusbarstrs.cameraheight}m   `;
				}
				if (state.fps === true) {
					result += `FPS${state.statusbarstrs.fps}   `;
				}
				return result;
			},
			scalecontents(state) {
				return state.scalebarstr;
			},
			currentMapTime(state) {
				if(state.timecontrolstrs.currenttime==null) return;
				return moment(state.timecontrolstrs.currenttime).format(state.timecontrolstrs.timeformat);
			},

		},
		actions: {
			SetTimelineShow(timelineshow) {
				const originshow = this.timeline;
				if (originshow != timelineshow) {
					settimelinevisib(timelineshow);
					this.timeline = timelineshow;
				}
			},
			SetStatusBarShow(show) {
				const originshow = this.statusbar;
				if (originshow != show) {
					if (show === false) {
						Bus.VM.$off(Bus.SignalType.Scene_Mouse_Move, onscenemousemovegetlonlat);
						GlobalState.viewer.scene.postRender.removeEventListener(onrenderlist);
					}
					else {
						this.statusbarstrs._fpsFrameCount = 0;
						Bus.VM.$on(Bus.SignalType.Scene_Mouse_Move, onscenemousemovegetlonlat);
						GlobalState.viewer.scene.postRender.addEventListener(onrenderlist);
						this.statusbarstrs._lastFpsTm = new Date();
					}
					this.statusbar = show;
				}
			},
			SetAnimationBarShow(show) {
				const originshow = this.animation;
				if (originshow != show) {
					GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
						subele.style.left = show !== true ? '0' : '268px';
					});
					this.animation = show;
				}
				GlobalState.viewer.clock.onTick.removeEventListener(onmapclocktick);
				if(show===true){
					GlobalState.viewer.clock.onTick.addEventListener(onmapclocktick);
				}
			},

			toggleshouldanimate() {
				this.timecontrolstrs.shouldanimate = !this.timecontrolstrs.shouldanimate;
				GlobalState.viewer.clock.shouldAnimate = this.timecontrolstrs.shouldanimate;

			},
			BackToNow(){
				Cesium.JulianDate.now(GlobalState.viewer.clock.currentTime)
			},
			SetAniStep(){
				GlobalState.viewer.clock.multiplier=this.timecontrolstrs.step;
			}
		}
	})
;

function settimelinevisib(visible) {
	if (visible === false) {
		GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
			GlobalState.viewer.tooltipcontainer.parentNode.removeChild(subele);
		})
	}
	else {
		//timeline
		GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
			subele.style.right = '0';
			subele.style.left = othersettings().animation !== true ? '0' : '268px';
			GlobalState.viewer.tooltipcontainer.parentNode.appendChild(subele);
		});
	}
}



function onscenemousemovegetlonlat(e) {
	const wgspos = GetPickedRayPositionWGS84(e.endPosition);
	if (wgspos == null) return;
	othersettings().statusbarstrs.lon = wgspos.longitude.toFixed(othersettings().lonlatpre);
	othersettings().statusbarstrs.lat = wgspos.latitude.toFixed(othersettings().lonlatpre);
	if (wgspos.height < 0) wgspos.height = 0;
	othersettings().statusbarstrs.height = wgspos.height.toFixed(0);
}

function onrenderlist() {
	const camera = GetCamera();
	othersettings().statusbarstrs.heading = camera.heading.toFixed(2) + '';
	othersettings().statusbarstrs.pitch = camera.pitch.toFixed(2) + '';
	othersettings().statusbarstrs.roll = camera.roll.toFixed(2) + '';
	othersettings().statusbarstrs.cameraheight = camera.height.toFixed(2);
	othersettings().statusbarstrs._fpsFrameCount++;
	const curtimesp = new Date();
	const fpstime = curtimesp - othersettings().statusbarstrs._lastFpsTm;
	if (fpstime > 1000) {
		let fps = (othersettings().statusbarstrs._fpsFrameCount * 1000) / fpstime;
		othersettings().statusbarstrs.fps = parseInt(fps) + ' FPS';
		othersettings().statusbarstrs._lastFpsTm = curtimesp;
		othersettings().statusbarstrs._fpsFrameCount = 0;
	}
	let tilesToRender = GlobalState.viewer.scene.globe._surface._tilesToRender;
	if (tilesToRender != null && tilesToRender.length > 0) {
		othersettings().statusbarstrs.lod = tilesToRender[0].level;
	}
}

function onscalerenderlist() {
	var scene = GlobalState.viewer.scene;
	const centerw = scene.canvas.width / 2;
	const height = scene.canvas.height;
	const wgs1 = GetPickedRayPositionWGS84({
		x: centerw, y: height
	});
	const wgs2 = GetPickedRayPositionWGS84({
		x: centerw, y: height - 65
	});
	if (wgs1 == null || wgs2 == null) {
		othersettings().scalebarstr = '';
		return;
	}
	wgs1.height = wgs2.height;
	const dis = Cesium.Cartesian3.distance(wgs1.ToCartesian(), wgs2.ToCartesian())
	// const dis=coordinates.CoordinateWGS84.GetDistancePlane(wgs1,wgs2);
	if (dis > 1000)
		othersettings().scalebarstr = parseInt(dis / 1000) + "km";
	else
		othersettings().scalebarstr = parseInt(dis) + "m";
}

function onmapclocktick() {
	othersettings().timecontrolstrs.currenttime=Cesium.JulianDate.toDate(GlobalState.viewer.clock.currentTime);
}




export function SetScaleBarShow(show) {
	const originshow = othersettings().scale;
	if (originshow != show) {
		if (show === false) {
			GlobalState.viewer.scene.postRender.removeEventListener(onscalerenderlist);
		}
		else {
			GlobalState.viewer.scene.postRender.addEventListener(onscalerenderlist);
		}
		othersettings().scale = show;
	}
}

export function InitBtns(settings) {
	if(settings)
	{
		tooltipbtns.forEach((btn, index) => {
			btn.show = settings[btn.key];
			if (btn.show === false) {
				GlobalState.viewer.mapcontainerbtns[index].forEach(subele => {
					GlobalState.viewer.tooltipcontainer.removeChild(subele);
				})
			}
		});
		othersettings().timeline = settings.timeline;
		othersettings().animation = settings.animation;
		othersettings().statusbar = settings.statusbar;
		othersettings().lonlat = settings.lonlat;
		othersettings().height = settings.height;
		othersettings().lod = settings.lod;
		othersettings().heading = settings.heading;
		othersettings().pitch = settings.pitch;
		othersettings().roll = settings.roll;
		othersettings().cameraheight = settings.cameraheight;
		othersettings().fps = settings.fps;
		othersettings().lonlatpre = settings.lonlatpre;
		//
		othersettings().scale = settings.scale;
		othersettings().scaleleft = settings.scaleleft;
		othersettings().scaleright = settings.scaleright;
		othersettings().scaletop = settings.scaletop;
		othersettings().scalebottom = settings.scalebottom;
	}
	else{
		tooltipbtns.forEach((btn, index) => {
			btn.show = true;
		});
	}



	GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
		subele.style.right = '0';
		subele.style.left = othersettings().animation !== true ? '0' : '268px';
	});
	const oriresizef = GlobalState.viewer.timeline.resize;
	GlobalState.viewer.timeline.resize = function () {
		oriresizef.apply(GlobalState.viewer.timeline);
		GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
			subele.style.right = '0';
			subele.style.left = othersettings().animation !== true ? '0' : '268px';
		});
	}
	if (othersettings().timeline === false) {
		GlobalState.viewer.mapcontainerbtns[7].forEach(subele => {
			GlobalState.viewer.tooltipcontainer.parentNode.removeChild(subele);
		});
	}

	if (othersettings().statusbar === true) {
		othersettings().statusbarstrs._fpsFrameCount = 0;
		Bus.VM.$on(Bus.SignalType.Scene_Mouse_Move, onscenemousemovegetlonlat);
		GlobalState.viewer.scene.postRender.addEventListener(onrenderlist);
		othersettings().statusbarstrs._lastFpsTm = new Date();
	}

	if (othersettings().scale === true) {
		GlobalState.viewer.scene.postRender.addEventListener(onscalerenderlist);
	}

	//time
	GlobalState.viewer.clock.onTick.removeEventListener(onmapclocktick);
	if(othersettings().animation===true){
		GlobalState.viewer.clock.onTick.addEventListener(onmapclocktick);
	}
}

export function GetSettings() {
	const result = {};
	tooltipbtns.forEach(item => {
		result[item.key] = item.show;
	});
	for (let key in othersettings().$state) {
		if (key != 'statusbarstrs' &&
			key != 'scalebarstr' &&
			key != 'timecontrolstrs'
		) {
			result[key] = othersettings()[key];
		}
	}
	return result;
}

function findindex(ind) {
	return tooltipbtns.findIndex((item, index) => index > ind && item.show === true);
}

function showindex(index) {
	let nextidx = findindex(index);
	if (nextidx == -1) {
		GlobalState.viewer.mapcontainerbtns[index].forEach(ele => {
			GlobalState.viewer.tooltipcontainer.appendChild(ele);
		})
	}
	else {
		let i = GlobalState.viewer.mapcontainerbtns[index].length - 1;
		let ele = GlobalState.viewer.mapcontainerbtns[nextidx][0];
		do {
			GlobalState.viewer.tooltipcontainer.insertBefore(
				GlobalState.viewer.mapcontainerbtns[index][i], ele);
			ele = GlobalState.viewer.mapcontainerbtns[index][i];
			i--;
		} while (i >= 0);
	}
}

function hidenindex(index) {
	GlobalState.viewer.mapcontainerbtns[index].forEach(ele => {
		GlobalState.viewer.tooltipcontainer.removeChild(ele);
	})
}

function setindexvisib(index, visibility) {
	if (visibility === true) {
		showindex(index);
	}
	else {
		hidenindex(index);
	}
}

export function SetToolbarBtnShow(key, show) {
	const curindex = tooltipbtns.findIndex(item => item.key == key);
	if (curindex == -1) return;
	const originshow = tooltipbtns[curindex].show;
	if (originshow != show) {
		setindexvisib(curindex, show);
		tooltipbtns[curindex].show = show;
	}
}






