<template>
    <div id="studio">
        <div id="FySceneContainer">
        </div>
        <div id="Statusbar" v-if="otS.statusbar" :style="otS.timeline===true?'bottom:30px;':''">{{otS.statuscontents}}</div>
        <div id="Scalebar" v-if="otS.scale&&otS.scalecontents!=''" :style="getscalestyle()">
            <div class="scalelabel">{{otS.scalecontents}}</div>
            <div class="scaleb"></div>
        </div>
        <div id="Animationbar" v-if="otS.animation">
            <div @click="otS.toggleshouldanimate()" :title="otS.timecontrolstrs.shouldanimate?'':''">
                <svg v-if="!otS.timecontrolstrs.shouldanimate" width="20" height="20" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><rect width="48" height="48" fill="white" fill-opacity="0.01"></rect><path d="M15 24V11.8756L25.5 17.9378L36 24L25.5 30.0622L15 36.1244V24Z" fill="none" stroke="#ffffff" stroke-width="5" stroke-linejoin="round"></path></svg>
                <svg v-if="otS.timecontrolstrs.shouldanimate" width="20" height="20" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><rect width="48" height="48" fill="white" fill-opacity="0.01"></rect><path d="M16 12V36" stroke="#ffffff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"></path><path d="M32 12V36" stroke="#ffffff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"></path></svg>
            </div>
            <div title="" @click="otS.BackToNow()">
                <svg width="20" height="20" style="margin-top: 6px;" viewBox="0 0 48 58" fill="none" xmlns="http://www.w3.org/2000/svg"><rect width="48" height="48" fill="white" fill-opacity="0.01"></rect><rect width="48" height="58" fill="white" fill-opacity="0.01"></rect><path d="M34.0234 6.68921C31.0764 4.97912 27.6525 4 24 4C12.9543 4 4 12.9543 4 24C4 35.0457 12.9543 44 24 44C35.0457 44 44 35.0457 44 24C44 20.3727 43.0344 16.9709 41.3461 14.0377" stroke="#ffffff" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"></path><path d="M31.9498 16.0502C31.9498 16.0502 28.5621 25.0947 27.0001 26.6568C25.438 28.2189 22.9053 28.2189 21.3432 26.6568C19.7811 25.0947 19.7811 22.562 21.3432 20.9999C22.9053 19.4378 31.9498 16.0502 31.9498 16.0502Z" fill="none" stroke="#ffffff" stroke-width="4" stroke-linejoin="round"></path></svg>
            </div>
            <span>{{otS.currentMapTime}}</span>
            <el-input-number
                    v-model="otS.timecontrolstrs.step"
                    :min="-10"
                    :max="10"
                    controls-position="right"
                    @change="otS.SetAniStep()"
            />
        </div>
    </div>
</template>

<script setup>
	import { CesiumViewer  } from '../buss/maphandler';
	import {othersettings} from "./Widgets/buss/widgets";
    import {ref,onMounted} from 'vue'

    const otS=othersettings();

    function getscalestyle(){
    	let horizon='left:80px',vertical='bottom:30px';

		if(otS.scaleleft!=null&&otS.scaleleft!='') horizon=`left:${otS.scaleleft}`;
        else if(otS.scaleright!=null&&otS.scaleright!='')horizon=`right:${otS.scaleright}`;


		if(otS.scaletop!=null&&otS.scaletop!='') vertical= `top:${otS.scaletop}`;
		else if(otS.scalebottom!=null&&otS.scalebottom!='') vertical= `bottom:${otS.scalebottom}`;


    	return `${horizon};${vertical};`
    }




	onMounted(() => {
        debugger
        CesiumViewer([], 'FySceneContainer');
	});


</script>



<style lang="less">
    #studio{
        width:100%;height:100%;overflow:hidden;
        position:relative;
        #FySceneContainer{
            width:100%;height:100%;
            text-align: left;
            .cesium-baseLayerPicker-choices{
                /*display: flex;*/
            }
        }
        #Statusbar{
            padding: 3px 10px;
            font-size: 13px;
            color: #e9e9e9;
            background-color: #0006;
            min-height: 26px;
            pointer-events: none;
            /*width:50%;*/
            position:absolute;
            bottom:0;right:0;
            /*left:50%;*/

            text-align: right;
        }
        #Scalebar{
            position:absolute;
            width:65px;
            height:25px;
            pointer-events:none;
            -webkit-user-select:none;
            -webkit-user-select:none;
            .scalelabel{
                font-size: 13px;
                color: #fff;
                text-align: center;
                width: 100%;
            }
            .scaleb{
               width:100%;
                left:0;
                position: absolute;
                height:10px;
                top:10px;
                border:1px solid white;
                border-top:none;
            }
        }
        #Animationbar{
            position:absolute;left:0;bottom:0;height:27px;width:268px;
            color:white;
            z-index: 999;
            background: linear-gradient(to bottom,#747577cc,#3a4452cc 11%,#2e3238cc,#353535cc 81%,#353535cc);
            >div:not(.el-input-number){
                display: inline-block;
                vertical-align: middle;
                /*padding: 1px 8px;*/
                border: solid 1px #888;
                color: #fff;
                background-color: #3f4854b3;
                border: none;
                cursor: pointer;
                width: 24px;
            }
            >span{
                margin: 0 4px;
                font-size: 13px;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }
            >div.el-input-number {
                width: 80px;
                height: 24px;

                outline:none;
                padding: 0 3px;
                margin: 0;
                color: #fff;
                background: #181b24;
                border: 1px solid #2d2f37;
                .el-input{
                    --el-input-border-color:transparent;
                    --el-input-text-color:white;


                }

                .el-input__wrapper {
                    background-color: transparent;
                    padding-right: 22px;
                    padding-left: 10px;
                }
            }
            >div.el-input-number.is-controls-right .el-input-number__decrease, >div.el-input-number.is-controls-right .el-input-number__increase{
                --el-input-number-controls-height: 10px;
                --el-fill-color-light:transparent;
                --color:white;
                opacity: 0;
                border:none;
                width:20px;
            }


            >div.el-input-number.is-controls-right:hover .el-input-number__decrease,
            >div.el-input-number.is-controls-right:hover .el-input-number__increase{

                opacity:1;
            }

        }
    }
</style>
