<template>
    <div id="maptree">
        <div class="scene-tree-header">
            
            <div>
                <div class="imgcont">
                    <button title="" type="button" @click="shf(true)">
                        <span>
                            <svg
                                    width="14" height="14" viewBox="0 0 48 48" fill="none"
                                    xmlns="http://www.w3.org/2000/svg">
                                <path d="M5 8C5 6.89543 5.89543 6 7 6H19L24 12H41C42.1046 12 43 12.8954 43 14V40C43 41.1046 42.1046 42 41 42H7C5.89543 42 5 41.1046 5 40V8Z"
                                      fill="none" stroke="#FFFFFF" stroke-width="4" stroke-linejoin="round"></path><path
                                    d="M43 22H5" stroke="#FFFFFF" stroke-width="4" stroke-linejoin="round"></path><path
                                    d="M5 16V28" stroke="#FFFFFF" stroke-width="4" stroke-linecap="round"
                                    stroke-linejoin="round"></path><path d="M43 16V28" stroke="#FFFFFF" stroke-width="4"
                                                                         stroke-linecap="round"
                                                                         stroke-linejoin="round"></path>
                            </svg>
                        </span>
                    </button>
                </div>
                <div class="imgcont">
                    <button title="" type="button" @click="opendia"><span>
                        <svg
                                width="14" height="14" viewBox="0 0 48 48" fill="none"
                                xmlns="http://www.w3.org/2000/svg"><rect
                                x="6" y="6" width="36" height="36" rx="3" fill="none" stroke="#FFFFFF" stroke-width="4"
                                stroke-linejoin="round"></rect><path d="M24 16V32" stroke="#FFFFFF" stroke-width="4"
                                                                     stroke-linecap="round"
                                                                     stroke-linejoin="round"></path><path
                                d="M16 24L32 24" stroke="#FFFFFF" stroke-width="4" stroke-linecap="round"
                                stroke-linejoin="round"></path></svg></span></button>
                </div>
            </div>
        </div>
        <div class="scene-tree-folder" v-if="showfolder">
            <el-input></el-input>
            <span>
                <svg width="14" height="14" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><path
                        d="M43 11L16.875 37L5 25.1818" stroke="#FFFFFF" stroke-width="4" stroke-linecap="round"
                        stroke-linejoin="round"></path></svg>
            </span>
            <span @click="shf(false)">
                <svg width="14" height="14" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><path
                        d="M8 8L40 40" stroke="#FFFFFF" stroke-width="4" stroke-linecap="round"
                        stroke-linejoin="round"></path><path d="M8 40L40 8" stroke="#FFFFFF" stroke-width="4"
                                                             stroke-linecap="round"
                                                             stroke-linejoin="round"></path></svg>
            </span>
        </div>
        <el-tree :data="mtSt.treedata"
                 show-checkbox
                 default-expand-all
                 node-key="id"
                 highlight-current >
        </el-tree>
        <div id="selectlayer">
            <el-dialog v-model="showselectlayerdialog" title="">
                <div>
                    <div v-for="(item,index) in onlineterrains">
                        <div>
                            <div>
                                <img :src="item.iconUrl">
                                <div :class="'check-cont'+(item.name===selectedterrainname?' selected':'')" @click="handleselectedclick(item)"></div>
                            </div>
                            <div>{{item.name}}</div>
                        </div>
                    </div>
                </div>
                <!--  -->
                <div class="pageChange" v-if="layernum>0">
                    <div class="prevButton" @click="prevClick" v-show="pageno > 1"></div>
                    <el-pagination
                            :page-size="pagesize"
                            :pager-count="5"
                            :current-page="pageno"
                            layout="pager"
                            :total="layernum"
                            @current-change="querylayers"
                            :small="true"
                    />
                    <!-- <div class="extra" v-show="searchParams.total < searchParams.realTotal">...</div> -->
                    <div class="nextButton" @click="nextClick" v-show="Math.ceil(scenenum/pagesize)>pageno"></div>
                </div>

                <el-footer>
                    <el-button @click="showselectlayerdialog.value=false"></el-button>
                    <el-button type="primary" @click="onterrainchanged"></el-button>
                </el-footer>
            </el-dialog>
        </div>
    </div>
</template>

<script setup>

	import {maptreesettings } from "./buss/maptree";

    import {ref,onMounted} from 'vue'
	const mtSt=maptreesettings();

	const showfolder=ref(false);
	const showselectlayerdialog=ref(false);
	const layers=ref([]);
	// const {proxy} = getCurrentInstance();
	const pagesize = ref(12);
	const pageno = ref(1);
	const layernum = ref(0);

    function shf(show) {
        showfolder.value=show;
	}
	function opendia() {
		showselectlayerdialog.value=true;
	}

	function resetlistchecked() {
		layers.value.forEach(item => {
			item.checked = false;
		});
	}

	function querylayers(){
		// proxy
		// 	.$post(FyConfig.IP+"/resource/layerAccess/queryUserPage", {
		// 		pageSize: pagesize.value,
		// 		pageNo: pageno.value,
		// 		userId: getSession('UserID', true)
		// 	})
		// 	.then((res) => {
		// 		debugger
		// 		layernum.value = Number(res.data.total);
		// 		layers.value = res.data.records;
		// 		resetlistchecked();
        //
		// 	});
    }

	onMounted(()=>{

    })

</script>

<style lang="less" scoped>
    #maptree {
        color: white;
        .scene-tree-header {
            height: 44px;
            line-height: 44px;
            padding: 0 13px;
            margin-bottom: 12px;
            border-top: 1px solid #42444c;
            border-bottom: 1px solid #42444c;
            font-size: 14px;
            display: flex;
            justify-content: space-between;
            > div {
                display: inline-flex;
                .imgcont {
                    > img {
                        width: 123px;
                        height: 123px;
                        border-radius: 4px;
                        object-fit: contain;
                        display: block;

                    }
                    > button {
                        display: inline-block;
                        height: 30px;
                        line-height: 30px;
                        background: #181b24;
                        border: 1px solid #2d2f37;
                        border-radius: 4px;
                        font-size: 12px;

                        font-weight: 500;
                        color: #fff;
                        padding: 0 5px;
                        cursor: pointer;
                        min-width: 30px;
                        text-align: center;
                        vertical-align: middle;

                        > span {
                            font-size: 18px !important;
                            line-height: 18px;
                            vertical-align: middle !important;
                            padding-left: 2px;
                            padding-right: 2px;
                        }
                    }
                }
            }

        }
        .scene-tree-folder{
            padding-left: 13px;
            padding-right: 13px;
            margin-bottom: 12px;
            display: flex;
            align-items: center;
            box-sizing: border-box;
            .el-input{
                width: 100%;
                height: 32px;
                font-size: 12px;
                color: #fff;
                line-height: 32px;
                text-align: left;
                background: #181b24;
                border: 1px solid #2d2f37;
                border-radius: 4px;
            }
            >span{
                display: inline-block;
                margin-left: 8px;
            }
        }
        .pageChange {
            position: sticky;
            background: transparent;

            margin: auto;
            width: 240px;
            height: 50px;
            display: flex;
            justify-content: space-around;
            flex-direction: row;
            font-size: 20px;
            .prevButton, .nextButton {
                line-height: 50px;
            }
        }
    }
</style>
<style lang="less">
    @import url('../../assets/css/scene.less');

    #selectlayer{
        .el-dialog{
            --el-dialog-bg-color:transparent;
            border-radius:4px;
            .el-dialog__header{
                background-color: #2d2f38;
                border-radius: 4px 4px 0 0;
                margin-right:0;

                .el-dialog__title{
                    color:white;
                }
            }
            .el-dialog__body{
                background-color: #232630;
                min-height:400px;
                .el-tabs__item{
                    color:white;
                }
                .el-tabs__content{
                    .el-tab-pane{
                        display:flex;
                        >div{
                            width:180px;
                            height:200px;
                            /*padding:12px 0px;*/
                            >div{
                                padding:10px;
                                >div:first-child{
                                    position: relative;
                                    width: 100%;
                                    background: black;
                                    img{
                                        margin:0 auto;
                                        width:100px;height:100px;
                                        display: block;
                                    }
                                    .check-cont{
                                        top:8px;
                                        right:6px;
                                        display:none;
                                    }
                                    .check-cont.selected{
                                        display:block;
                                    }
                                }
                                >div:last-child{
                                    text-align: center;
                                    width:100%;
                                    height:40px;
                                    line-height: 40px;
                                    color:white;
                                    background-color: #2d2f38;
                                }
                            }
                            >div:hover{
                                >div:first-child{
                                    .check-cont{
                                        display:block;
                                    }
                                }


                            }


                        }
                    }

                }
                .el-tabs__nav-wrap::after{
                    display: none;}
            }
        }

    }
</style>
