<template>
    <div id="terrainmap">
        <el-collapse v-model="activeNames">
            <el-collapse-item title="TerrainSettings" name="1">
                <div class="swichcontent contentright">
                    <span>TerrainEnable:</span>
                    <el-switch v-model="trSt.showterrain" @change="trSt.SetShowTerrain()"/>
                </div>
                <div class="swichcontent contentright">
                    <span>TerrainSelect:</span>
                    <div>
                        <span>{{trSt.terrainname}}</span>
                        <!--<span>{{curterrainname}}</span>-->
                        <el-button @click="opendia">Select</el-button>
                    </div>
                </div>
            </el-collapse-item>
            <el-collapse-item title="RenderEffect" name="2">
                <div class="swichcontent">
                    <span>Triangle Network:</span>
                    <el-switch v-model="trSt.shownetwork" @change="trSt.SetShowNetWork()"/>
                </div>
                <div class="swichcontent">
                    <span>exaggerate:</span>
                    <el-input-number
                            v-model="trSt.terrainexa"
                            @change="trSt.SetTerrainExa()"
                            :min="-100"
                            :max="100"
                            :step="0.1"
                            controls-position="right"
                    />
                </div>
                <el-divider></el-divider>
                <div class="swichcontent">
                    <span>Contour:</span>
                    <el-switch v-model="trSt.showcontour" @change="trSt.SetGlobeMaterial()"/>
                </div>
                <div class="swichcontent" v-if="trSt.showcontour">
                    <span>LineWidth:</span>
                    <el-slider v-model="trSt.contourwidth" :min="0" :max="15" :step="1"
                               @change="trSt.SetGlobeMaterial()"/>
                </div>
                <div class="swichcontent" v-if="trSt.showcontour">
                    <span>Step(m):</span>
                    <el-slider v-model="trSt.contourstep" :min="0" :max="1000" :step="10"
                               @change="trSt.SetGlobeMaterial()"/>
                </div>
                <div class="swichcontent" v-if="trSt.showcontour">
                    <span>Color:</span>
                    <el-color-picker v-model="trSt.contourcss" @change="trSt.SetGlobeMaterial()"/>
                </div>
                <el-divider></el-divider>
                <div class="swichcontent">
                    <span>GroundRender:</span>
                    <el-switch v-model="trSt.groundrender" @change="trSt.SetGlobeMaterial()"/>
                </div>
                <div class="swichcontent" v-if="trSt.groundrender">
                    <span>Type:</span>
                    <el-select popper-class="popper" v-model="trSt.rendertype" placeholder="" @change="trSt.SetGlobeMaterial()">
                        <el-option label="" :value="1"/>
                        <el-option label="" :value="2"/>
                        <el-option label="" :value="3"/>
                    </el-select>
                </div>
                <div class="swichcontent" v-if="trSt.groundrender">
                    <span>Colors:</span>
                    <el-select popper-class="popper colorpopper" class="colorselect" v-model="trSt.imagestripe"
                               :style="getcolorrampstyle(trSt.colorramps[trSt.imagestripe-1],false)" @change="trSt.SetGlobeMaterial()">
                        <el-option v-for="(item,index) in trSt.colorramps" :value="(index+1)" :label="' '">
                            <span :style="getcolorrampstyle(item)"></span>
                        </el-option>
                    </el-select>
                </div>
                <div class="swichcontent" v-if="trSt.groundrender&&trSt.rendertype==1">
                    <span>MinHeight:</span>
                    <el-slider v-model="trSt.minranphei" :min="0" :max="10000" :step="10"
                               @change="trSt.SetGlobeMaterial()"/>
                </div>
                <div class="swichcontent" v-if="trSt.groundrender&&trSt.rendertype==1">
                    <span>MaxHeight:</span>
                    <el-slider v-model="trSt.maxranphei" :min="0" :max="10000" :step="10"
                               @change="trSt.SetGlobeMaterial()"/>
                </div>
            </el-collapse-item>
            <el-collapse-item title="UnderGround" name="3">
                <div class="swichcontent">
                    <span>UnableUnderGround:</span>
                    <el-switch v-model="trSt.enableunder" @change="trSt.SetEnableUnder()"/>
                </div>
                <div class="swichcontent">
                    <span>GlobeTransparent:</span>
                    <el-switch v-model="trSt.globea" @change="trSt.SetGroundA()"/>
                </div>
            </el-collapse-item>
        </el-collapse>
        <div id="selectlayerterrain">
            <el-dialog v-model="showselectlayerdialog" title="SelectTerrain">
                <el-tabs v-model="dialogactivename" class="demo-tabs" @tab-click="handleDialogTabClick">
                    <el-tab-pane label="OnlineTerrain" name="onlineterrain">
                        <div v-for="(item,index) in onlineterrains">
                            <div>
                                <div>
                                    <img :src="item.iconUrl">
                                    <div :class="'check-cont'+(item.name===selectedterrainname?' selected':'')" @click="handleselectedclick(item)"></div>
                                </div>
                                <div>{{item.name}}</div>
                            </div>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="" name="interterrain"></el-tab-pane>
                </el-tabs>
                <el-footer>
                    <el-button @click="showselectlayerdialog.value=false"></el-button>
                    <el-button type="primary" @click="onterrainchanged"></el-button>
                </el-footer>
            </el-dialog>
        </div>
    </div>
</template>

<script setup>
    import {ref,onMounted} from 'vue'
	import {GlobalState} from "../../buss/maphandler";
    import {terrainsettings} from "./buss/terrainMap";
	const trSt=terrainsettings();

	const activeNames = ref(['1', '2', '3']);
	const dialogactivename=ref('onlineterrain');

    const showselectlayerdialog=ref(false);
	const selectedterrainname=ref('onlineterrain');

	const onlineterrains=ref([]);
	const colorramps=ref([

		[ "#000000","#2747E0", "#D33B7D","#D33038","#FF9742","#ffd700","#ffffff"],

    ]);

	function getcolorrampstyle(item,dropdown=true) {
		// const duan=item.length-1;
		// const per=100/duan;
		// const sets=[{color:item[0],per:'0%'}];
		// for(let i=1;i<item.length-1;i++){
		// 	sets.push({color:item[i],per:(per*i).toString()+"%"})
        // }
        // sets.push({color:item[item.length-1],per:'100%'});
        const sets=trSt.getcolorrampst(item);

		const re= `${dropdown===true?'height:20px;width:100%;':''}display:inline-block;background: linear-gradient(to right,${sets.map(item=>{
			return item.color+" "+(item.per*100).toString()+"%";
        }).join(',')})`;

		//return "background-image: linear-gradient(to right,rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5));width:100%;display:inline-block;";
		return re;
	}
    function opendia() {
		showselectlayerdialog.value=true;
	}


	function handleDialogTabClick() {

	}
	function handleselectedclick(item) {
		if(selectedterrainname.value==item.name){
			selectedterrainname.value='';
		}
		else{
			selectedterrainname.value=item.name;
		}
	}

	function onterrainchanged(){
    	trSt.SetTerrain(selectedterrainname.value);
		showselectlayerdialog.value=false;
    }

	onMounted(()=>{
		const len=GlobalState.viewer.baseLayerPicker._viewModel.terrainProviderViewModels.length;

		for(let i=0;i<len;i++){
			onlineterrains.value.push(GlobalState.viewer.baseLayerPicker._viewModel.terrainProviderViewModels[i]);
		}

		selectedterrainname.value=trSt.terrainname;
	})


</script>

<style scoped>

</style>
<style lang="less">
    @import url('../../assets/css/scene.less');

    #selectlayerterrain{
        .el-dialog{
            --el-dialog-bg-color:transparent;
            border-radius:4px;
            .el-dialog__header{
                background-color: #2d2f38;
                border-radius: 4px 4px 0 0;
                margin-right:0;

                .el-dialog__title{
                    color:white;
                }
            }
            .el-dialog__body{
                background-color: #232630;
                .el-tabs__item{
                    color:white;
                }
                .el-tabs__content{
                    .el-tab-pane{
                        display:flex;
                        >div{

                            width:180px;
                            height:200px;
                            /*padding:12px 0px;*/
                            >div{
                                padding:10px;

                                >div:first-child{
                                    position: relative;
                                    width: 100%;
                                    background: black;
                                    img{
                                        margin:0 auto;
                                        width:100px;height:100px;
                                        display: block;
                                    }
                                    .check-cont{
                                        top:8px;
                                        right:6px;
                                        display:none;
                                    }
                                    .check-cont.selected{
                                        display:block;
                                    }
                                }
                                >div:last-child{
                                    text-align: center;
                                    width:100%;
                                    height:40px;
                                    line-height: 40px;
                                    color:white;
                                    background-color: #2d2f38;
                                }
                            }
                            >div:hover{
                                >div:first-child{
                                    .check-cont{
                                        display:block;
                                    }
                                }


                            }


                        }
                    }

                }
                .el-tabs__nav-wrap::after{
                    display: none;}
            }
        }

    }
</style>
<style lang="less">
    @import url('./css/style.less');
</style>
