import {defineStore} from "pinia";

import {circumsettings} from "./circum";
import {GetCamera,GlobalState} from "../../../buss/maphandler";
import {KeyBoardControl} from "../../../buss/keyboardcontrol";

export const camerasettings = defineStore('camerasettings', {
	state: () => {
		return {
			originlon: 110.603252444485,
			originlat: 27.941885505227617,
			originhei: 9491663.9170652,
			originheading: 0,
			originpitch: -90,
			originroll: 0,

			zoomF:5,
			minizoomdis:1,
			maxizoomdis:9000000,
			minicolhei:15000,

			allowrotate:true,
			allowtranslate:true,
			allowtilt:true,
			allowzoom:true,
			rightrotate:false,

			allowkeyboard:false,
			panstep:10,
			centerrotatestep:1,
			camerarotatestep:1,
			minpitch:-90,
			maxpitch:90,
			minhei:0,

		}
	},
	actions: {
		SetOriginCamera(zoomto = false) {
			const scope=this;
			const viewer=GlobalState.viewer;
			const f = function () {
				viewer.camera.flyTo({
					destination: Cesium.Cartesian3.fromDegrees(scope.originlon, scope.originlat, scope.originhei),
					orientation: {
						heading: Cesium.Math.toRadians(scope.originheading),
						pitch:Cesium.Math.toRadians(scope.originpitch),
						roll: Cesium.Math.toRadians(scope.originroll)
					}
				});
			}
			if (zoomto === true) {
				f();
			}

			viewer.homeButton.viewModel.command.beforeExecute._listeners=[
				function (e) {
				if(e){e.cancel = true;}
				f();
			}]
		},

		Locate(){
			GlobalState.viewer.homeButton.viewModel.command.beforeExecute._listeners[0]();
		},

		SetCurOrigin(){
			const c=GetCamera();
			this.originlon=c.longitude;
			this.originlat=c.latitude;
			this.originhei=c.height;
			this.originheading=c.heading;
			this.originpitch=c.pitch;
			this.originroll=c.roll;
		},

		SetZoomF(){
			GlobalState.viewer.scene.screenSpaceCameraController._zoomFactor=this.zoomF;
		},
		SetMiniZoomDis(){
			GlobalState.viewer.scene.screenSpaceCameraController.minimumZoomDistance=this.minizoomdis;
			const scope=this;
			GlobalState.viewer.mapcontainerbtns[2][0].onclick=function(){
				let Camera = GetCamera();

				if(Camera.height<scope.minizoomdis) return;
				// const m=GlobalState.viewer.camera.getMagnitude();
				// if(m<scope.minizoomdis) return;
				GlobalState.viewer.camera.zoomIn(Camera.height/10);
			}
		},
		SetMaxiZoomDis(){
			GlobalState.viewer.scene.screenSpaceCameraController.maximumZoomDistance =this.maxizoomdis;
			const scope=this;
			GlobalState.viewer.mapcontainerbtns[2][1].onclick=function(){
				let Camera = GetCamera();
				if(Camera.height>scope.maxizoomdis) return;
				// const m=GlobalState.viewer.camera.getMagnitude();
				// if(m>scope.maxizoomdis) return;
				GlobalState.viewer.camera.zoomOut(Camera.height/10);
			}
		},
		SetMiniColHeight(){
			GlobalState.viewer.scene.screenSpaceCameraController.minimumCollisionTerrainHeight=this.minicolhei;
		},
		SetAllowRotate(){
			GlobalState.viewer.scene.screenSpaceCameraController.enableRotate=this.allowrotate;
		},
		SetAllowTranslate(){
			GlobalState.viewer.scene.screenSpaceCameraController.enableTranslate=this.allowtranslate;
		},
		SetAllowTilt(){
			GlobalState.viewer.scene.screenSpaceCameraController.enableTilt=this.allowtilt;
		},
		SetAllowZoom(){
			GlobalState.viewer.scene.screenSpaceCameraController.enableZoom=this.allowzoom;
		},
		SetRightRotate(){
			GlobalState.viewer.scene.screenSpaceCameraController.rotateEventTypes=
				this.rightrotate==true?
					[Cesium.CameraEventType.LEFT_DRAG,Cesium.CameraEventType.RIGHT_DRAG]:
					[Cesium.CameraEventType.LEFT_DRAG];
			GlobalState.viewer.scene.screenSpaceCameraController.zoomEventTypes=
				this.rightrotate==true?
					[Cesium.CameraEventType.WHEEL, Cesium.CameraEventType.PINCH]:
					[Cesium.CameraEventType.RIGHT_DRAG, Cesium.CameraEventType.WHEEL, Cesium.CameraEventType.PINCH]
		},

		SetAllowkeyboard(){
			if(this.allowkeyboard===true){
				KeyBoardControl.getInstance().SetParams(this);
			}
			KeyBoardControl.getInstance().SetControl(this.allowkeyboard);
		},

		SetPanStep(){
			KeyBoardControl.getInstance().SetParams({panstep:this.panstep});
		},
		SetMinHei(){
			KeyBoardControl.getInstance().SetParams({minhei:this.minhei});
		},
		SetCenterRotateStep(){
			KeyBoardControl.getInstance().SetParams({centerrotatestep:this.centerrotatestep});
		},
		SetCameraRotateStep(){
			KeyBoardControl.getInstance().SetParams({camerarotatestep:this.camerarotatestep});
		},
		SetMinPitch(){
			KeyBoardControl.getInstance().SetParams({minpitch:this.minpitch});
		},
		SetMaxPitch(){
			KeyBoardControl.getInstance().SetParams({maxpitch:this.maxpitch});
		}




	}
});

export function InitCameraSt(settings) {
	if (settings) {
		for (let key in settings) {
			camerasettings()[key] = settings[key];
		}
	}
	camerasettings().SetOriginCamera(true);
	camerasettings().SetZoomF();
	camerasettings().SetMiniZoomDis();
	camerasettings().SetMaxiZoomDis();
	camerasettings().SetMiniColHeight();
	camerasettings().SetAllowRotate();
	camerasettings().SetAllowTranslate();
	camerasettings().SetAllowTilt();
	camerasettings().SetAllowZoom();
	camerasettings().SetRightRotate();
	camerasettings().SetAllowkeyboard();
}

export function GetCameraSt() {
	const result = {};
	for (let key in camerasettings().$state) {
		result[key] = camerasettings()[key];
	}
	return result;
}
