<template>
    <div id="widgets">
        <el-collapse v-model="activeNames">
            <el-collapse-item title="Widgets Tools" name="1">
                <div>
                    Consistent with real life: in line with the process and logic of real
                    life, and comply with languages and habits that the users are used to;
                </div>
                <div>
                    Consistent within interface: all elements should be consistent, such
                    as: design style, icons and texts, position of elements, etc.
                </div>
            </el-collapse-item>
            <el-collapse-item title="Toolbar" name="2">
                <div class="swichcontent">
                    <span>Home:</span>
                    <el-switch v-model="settings.home"/>
                </div>
                <div class="swichcontent">
                    <span>Compass:</span>
                    <el-switch v-model="settings.compass"/>
                </div>
                <div class="swichcontent">
                    <span>Zoom:</span>
                    <el-switch v-model="settings.zoominout"/>
                </div>
                <div class="swichcontent">
                    <span>2D/3D:</span>
                    <el-switch v-model="settings.scenemode"/>
                </div>
                <div class="swichcontent">
                    <span>ImagePicker:</span>
                    <el-switch v-model="settings.imapk"/>
                </div>
                <div class="swichcontent">
                    <span>FullScreen:</span>
                    <el-switch v-model="settings.fullsize"/>
                </div>
                <div class="swichcontent">
                    <span>Navigation:</span>
                    <el-switch v-model="settings.navi"/>
                </div>
            </el-collapse-item>
            <el-collapse-item title="Statusbar" name="3">
                <div class="swichcontent">
                    <span>Enable:</span>
                    <el-switch v-model="settings.statusbar"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Longitude/Latitude:</span>
                    <el-switch v-model="settings.lonlat"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Height:</span>
                    <el-switch v-model="settings.height"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Level:</span>
                    <el-switch v-model="settings.lod"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Heading:</span>
                    <el-switch v-model="settings.heading"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Pitch:</span>
                    <el-switch v-model="settings.pitch"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Roll:</span>
                    <el-switch v-model="settings.roll"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>CameraHeight:</span>
                    <el-switch v-model="settings.cameraheight"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>FPS:</span>
                    <el-switch v-model="settings.fps"/>
                </div>
                <div class="swichcontent" v-if="settings.statusbar">
                    <span>Precision Lon/Lat:</span>
                    <el-input v-model="settings.lonlatpre"></el-input>
                </div>
            </el-collapse-item>
            <el-collapse-item title="Scalebar" name="4">
                <div class="swichcontent">
                    <span>Enable:</span>
                    <el-switch v-model="settings.scale"/>
                </div>
                <div class="swichcontent" v-if="settings.scale">
                    <span>Top:</span>
                    <el-input v-model="settings.scaletop"></el-input>
                </div>
                <div class="swichcontent" v-if="settings.scale">
                    <span>Bottom:</span>
                    <el-input v-model="settings.scalebottom"></el-input>
                </div>
                <div class="swichcontent" v-if="settings.scale">
                    <span>Left:</span>
                    <el-input v-model="settings.scaleleft"></el-input>
                </div>
                <div class="swichcontent" v-if="settings.scale">
                    <span>Right:</span>
                    <el-input v-model="settings.scaleright"></el-input>
                </div>
            </el-collapse-item>
            <el-collapse-item title="TimeControl" name="4">
                <div class="swichcontent">
                    <span>TimeScale:</span>
                    <el-switch v-model="settings.timeline"/>
                </div>
                <div class="swichcontent">
                    <span>Animation:</span>
                    <el-switch v-model="settings.animation"/>
                </div>
                <div class="swichcontent" v-if="settings.animation">
                    <span>Format:</span>
                    <el-input v-model="otS.timecontrolstrs.timeformat"></el-input>
                </div>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script setup>
	import {
		othersettings,
		SetToolbarBtnShow,
		GetSettings,
		SetScaleBarShow,
	} from "./buss/widgets";
    import {ref,onMounted,watch} from 'vue'

	const otS = othersettings();


	const activeNames = ref(['1', '2', '3', '4']);
	const settings = ref({
		// home: true,
		// compass: true,
		// zoominout: true,
		// scenemode: true,
		// imapk: true,
		// fullsize: true,
		// navi: true,
		//
		//
		//
		//
		// statusbar: true,
		// lonlat: true,
		// lonlatpre: 6,
		//
		// timeline:true,
	})



	onMounted(() => {
		//Bus.VM.$on
		//Bus.VM.$on(Bus.SignalType.SCENEBUILDINIT, function () {
			settings.value = GetSettings();
		//});
	});
	watch(() => settings.value.home, (newval, oldval) => {
		SetToolbarBtnShow('home', newval);
	}, {deep: true});
	watch(() => settings.value.compass, (newval, oldval) => {
		SetToolbarBtnShow('compass', newval);
	}, {deep: true});
	watch(() => settings.value.zoominout, (newval, oldval) => {
		SetToolbarBtnShow('zoominout', newval);
	}, {deep: true});
	watch(() => settings.value.scenemode, (newval, oldval) => {
		SetToolbarBtnShow('scenemode', newval);
	}, {deep: true});
	watch(() => settings.value.imapk, (newval, oldval) => {
		SetToolbarBtnShow('imapk', newval);
	}, {deep: true});
	watch(() => settings.value.fullsize, (newval, oldval) => {
		SetToolbarBtnShow('fullsize', newval);
	}, {deep: true});
	watch(() => settings.value.navi, (newval, oldval) => {
		SetToolbarBtnShow('navi', newval);
	}, {deep: true});
	watch(() => settings.value.timeline, (newval, oldval) => {
		otS.SetTimelineShow(newval);
	}, {deep: true});
	watch(() => settings.value.statusbar, (newval, oldval) => {
		otS.SetStatusBarShow(newval)
	}, {deep: true});
	watch(() => settings.value.lonlat, (newval, oldval) => {
		otS.lonlat = newval;
	}, {deep: true});
	watch(() => settings.value.height, (newval, oldval) => {
		otS.height = newval;
	}, {deep: true});
	watch(() => settings.value.lod, (newval, oldval) => {
		otS.lod = newval;
	}, {deep: true});
	watch(() => settings.value.heading, (newval, oldval) => {
		otS.heading = newval;
	}, {deep: true});
	watch(() => settings.value.pitch, (newval, oldval) => {
		otS.pitch = newval;
	}, {deep: true});
	watch(() => settings.value.roll, (newval, oldval) => {
		otS.roll = newval;
	}, {deep: true});
	watch(() => settings.value.cameraheight, (newval, oldval) => {
		otS.cameraheight = newval;
	}, {deep: true});
	watch(() => settings.value.fps, (newval, oldval) => {
		otS.fps = newval;
	}, {deep: true});
	watch(() => settings.value.lonlatpre, (newval, oldval) => {
		otS.lonlatpre = newval;
	}, {deep: true});
	watch(() => settings.value.scale, (newval, oldval) => {
		SetScaleBarShow(newval);
	}, {deep: true});
	watch(() => settings.value.scaleleft, (newval, oldval) => {
		otS.scaleleft = newval;
	}, {deep: true});
	watch(() => settings.value.scaleright, (newval, oldval) => {
		otS.scaleright = newval;
	}, {deep: true});
	watch(() => settings.value.scaletop, (newval, oldval) => {
		otS.scaletop = newval;
	}, {deep: true});
	watch(() => settings.value.scalebottom, (newval, oldval) => {
		otS.scalebottom = newval;
	}, {deep: true});
	watch(() => settings.value.animation, (newval, oldval) => {
		otS.SetAnimationBarShow(newval);
	}, {deep: true});


</script>

<style scoped>

</style>
<style lang="less">
    @import url('./css/style.less');
</style>
