import {GetCamera,GetPickedRayPositionWGS84,GlobalState} from "./maphandler";
import coordinates from '@/common/util/Coordinates'

function rotate2(x,y,anglerad) {
	// Rotate a point 45 degrees counterclockwise.
	const p = new Cesium.Cartesian2(x, y);
	const m = Cesium.Matrix2.fromRotation(anglerad);
	const rotated = Cesium.Matrix2.multiplyByVector(m, p, new Cesium.Cartesian2());
	return rotated;
}

export class KeyBoardControl {
	constructor() {
		this.instance = null;
		var flags = {
			moveForward: false,
			moveBackward: false,
			moveUp: false,
			moveDown: false,
			moveLeft: false,
			moveRight: false,
			centerRotateFor: false,
			centerRotateBak: false,
			centerRotateUp: false,
			centerRotateDown: false,
			centerRotateClock: false,
			centerRotateUnClock: false,
			cameraRotateUp: false,
			cameraRotateDown: false,
			cameraRotateLeft: false,
			cameraRotateRight: false,
		};
		this.pars = {
			panstep: 10,
			centerrotatestep: 1,
			camerarotatestep: 1,
			minpitch: -90,
			maxpitch: 90,
			minhei: 0,
		}

		function getFlagForKeyCode(keyCode) {
			switch (keyCode) {
				case 'Q'.charCodeAt(0):
					return 'moveUp';
				case 'E'.charCodeAt(0):
					return 'moveDown';
				case 'W'.charCodeAt(0):
					return 'moveForward';
				case 'S'.charCodeAt(0):
					return 'moveBackward';
				case 'D'.charCodeAt(0):
					return 'moveRight';
				case 'A'.charCodeAt(0):
					return 'moveLeft';
				case 'I'.charCodeAt(0):
					return 'centerRotateFor';
				case 'K'.charCodeAt(0):
					return 'centerRotateBak';
				case 'U'.charCodeAt(0):
					return 'centerRotateUp';
				case 'O'.charCodeAt(0):
					return 'centerRotateDown';
				case 'J'.charCodeAt(0):
					return 'centerRotateUnClock';
				case 'L'.charCodeAt(0):
					return 'centerRotateClock';
				case 38:
					return 'cameraRotateUp';
				case 40:
					return 'cameraRotateDown';
				case 37:
					return 'cameraRotateLeft';
				case 39:
					return 'cameraRotateRight';
				default:
					return undefined;
			}
		}

		this.keydown = function (e) {
			var flagName = getFlagForKeyCode(e.keyCode);
			if (typeof flagName !== 'undefined') {
				flags[flagName] = true;
			}
		}
		this.keyup = function (e) {
			var flagName = getFlagForKeyCode(e.keyCode);
			if (typeof flagName !== 'undefined') {
				flags[flagName] = false;
			}
		}
		const scope=this;
		function rotatecamera(addheading,addpitch) {
			const c=GlobalState.viewer.camera;
			GlobalState.viewer.camera.setView({
				destination : c.position,
				orientation: {
					heading : c.heading+addheading,
					pitch : c.pitch+addpitch,    // default value (looking down)
					roll : c.roll                             // default value
				}
			})
		}
		function rotatecenter( addheading, addpitch) {
			const centerwgs=GetPickedRayPositionWGS84(new Cesium.Cartesian2(
				GlobalState.viewer.scene.canvas.width/2,
				GlobalState.viewer.scene.canvas.height/2
			));
			if(centerwgs==null) return;

			const c=GlobalState.viewer.camera;
			const target=centerwgs.ToCartesian();
			let lc=coordinates.CoordinateLocal.FromCartesian(c.position,target);

			let heading=c.heading,pitch=c.pitch,roll=c.roll;
			if(addheading!=0){
				let xyn=rotate2(lc.x,lc.y,addheading);
				lc.x=xyn.x;
				lc.y=xyn.y;
				heading-=addheading;
			}
			else if(addpitch!=0){
				let ty=Math.sqrt(lc.x*lc.x+lc.y*lc.y);
				let pzn=rotate2(ty,lc.z,addpitch);
				lc.x=lc.x*pzn.x/ty;
				lc.y=lc.y*pzn.x/ty;
				lc.z=pzn.y;
				pitch-=addpitch;
				if(addpitch>0&&pitch<=Cesium.Math.toRadians(scope.pars.minpitch)) return;
				else if(addpitch<0&&pitch>=Cesium.Math.toRadians(scope.pars.maxpitch)) return;
			}
			const position=coordinates.CoordinateLocal.ToCartesian(lc,target);

			GlobalState.viewer.camera.setView({
				destination :position,
				orientation: {
					heading : heading,
					pitch : pitch,    // default value (looking down)
					roll : roll                             // default value
				}
			})

		}


		function panonplane(angle) {
			const p=GlobalState.viewer.camera.position;
			const d=GlobalState.viewer.camera.direction;
			const d1=new Cesium.Cartesian3(p.x+d.x,p.y+d.y,p.z+d.z);

			const l=coordinates.CoordinateLocal.FromCartesian(d1,p);
			l.z=0;
			const xy=rotate2(l.x,l.y,angle);
			l.x=xy.x;
			l.y=xy.y;
			const d2=coordinates.CoordinateLocal.ToCartesian(l,p);
			d2.x-=p.x;
			d2.y-=p.y;
			d2.z-=p.z;
			GlobalState.viewer.camera.move(d2,scope.pars.panstep);
		}


		this.ontick = function () {

			// // 
			// var cameraHeight = ellipsoid.cartesianToCartographic(camera.position).height;
			// var moveRate = cameraHeight / 100.0;
			const curcamera=GetCamera();
			if(curcamera.height<scope.pars.minhei) return;
			if (flags.moveForward) {
				panonplane(0);
			}
			if(flags.moveBackward){
				panonplane(Cesium.Math.PI);
			}


			if(flags.moveUp){
				const c=GetCamera();
				const wgs=new coordinates.CoordinateWGS84(c.longitude,c.latitude,c.height+scope.pars.panstep);
				GlobalState.viewer.camera.position=wgs.ToCartesian();

			}
			if(flags.moveDown){
				const c=GetCamera();
				const h=c.height-scope.pars.panstep;
				if(h<=scope.pars.minhei) return;
				const wgs=new coordinates.CoordinateWGS84(c.longitude,c.latitude,h);
				GlobalState.viewer.camera.position=wgs.ToCartesian();
			}
			if(flags.moveLeft){
				panonplane(Cesium.Math.PI/2);
			}
			if(flags.moveRight){
				panonplane(-Cesium.Math.PI/2);
			}

			if(flags.cameraRotateLeft){
				rotatecamera(Cesium.Math.toRadians(scope.pars.camerarotatestep)/10,0);
			}
			if(flags.cameraRotateRight){
				rotatecamera(Cesium.Math.toRadians(-scope.pars.camerarotatestep)/10,0);
			}
			if (flags.cameraRotateUp) {
				if(GlobalState.viewer.camera.pitch>=Cesium.Math.toRadians(scope.pars.maxpitch)) return;
				rotatecamera(0,Cesium.Math.toRadians(scope.pars.camerarotatestep)/10,0);
			}
			if (flags.cameraRotateDown) {
				if(GlobalState.viewer.camera.pitch<=Cesium.Math.toRadians(scope.pars.minpitch)) return;
				rotatecamera(0,Cesium.Math.toRadians(-scope.pars.camerarotatestep)/10,0);
			}
			if (flags.centerRotateFor) {
				GlobalState.viewer.camera.moveForward(scope.pars.panstep);
			}
			if(flags.centerRotateBak){
				GlobalState.viewer.camera.moveBackward(scope.pars.panstep);
			}
			if(flags.centerRotateClock){
				rotatecenter(-Cesium.Math.toRadians(scope.pars.centerrotatestep)/10,0);
			}
			if(flags.centerRotateUnClock){
				rotatecenter(Cesium.Math.toRadians(scope.pars.centerrotatestep)/10,0);
			}
			if(flags.centerRotateUp) {

				rotatecenter(0, Cesium.Math.toRadians(scope.pars.centerrotatestep) / 10);
			}
			if(flags.centerRotateDown) {

				rotatecenter(0, -Cesium.Math.toRadians(scope.pars.centerrotatestep) / 10);
			}
		}
	}

	static getInstance() {
		if (!this.instance) {
			this.instance = new KeyBoardControl();
		}
		return this.instance;
	}

	SetControl(control) {
		document.removeEventListener('keydown', this.keydown);
		document.removeEventListener('keyup', this.keyup);
		GlobalState.viewer.clock.onTick.removeEventListener(this.ontick);
		if (control === true) {
			document.addEventListener('keydown', this.keydown, false);
			document.addEventListener('keyup', this.keyup, false);
			GlobalState.viewer.clock.onTick.addEventListener(this.ontick);
		}
	}

	SetParams(options) {
		this.pars.panstep = options.panstep||this.pars.panstep;
		this.pars.centerrotatestep = options.centerrotatestep||this.pars.centerrotatestep;
		this.pars.camerarotatestep = options.camerarotatestep||this.pars.camerarotatestep;
		this.pars.minpitch = options.minpitch||this.pars.minpitch;
		this.pars.maxpitch = options.maxpitch||this.pars.maxpitch;
		this.pars.minhei = options.minhei||this.pars.minhei;
	}
}



