function translate(element, translatestrlist) {
	element.childNodes.forEach(ele => {
		if (ele.childNodes.length == 0) {
			const find = translatestrlist.find(item => item[0] == ele.textContent);
			if (find) {
				ele.textContent = find[1];
			}
		}
		else {
			translate(ele, translatestrlist);
		}
	})
}

