export function SetEntity(entity, replace = true) {
	let viewer = GlobalState.viewer;
	let entityori = viewer.entities.getById(entity.id);
	if (entityori) {
		viewer.entities.remove(entityori);
	}
	return viewer.entities.add(entity);
}

