/** 
 * @param e (Sat Nov 26 2022 21:37:29 GMT+0800 ())
 * @returns {string} (2022-11-26 21:37:29)
 */
export function timeFormatConvert(e) {
	const Y = e.getFullYear(); // 
	const M = prefixZero(e.getMonth() + 1); // 
	const D = prefixZero(e.getDate()); // 
	const H = prefixZero(e.getHours()); // 
	const Mi = prefixZero(e.getMinutes()); // 
	const S = prefixZero(e.getSeconds()); // 
	// return {Y + "-" + M + "-" + D + " " + H + ":" + Mi + ":" + S;}
	return {Y, M, D, H, Mi, S}
}

export function prefixZero(num = 0, n = 2) {
	// 
	return (Array(n).join("0") + num).slice(-n);
}


/**
 * 
 * @param isdaytrue12false12
 * @param viewer
 * @constructor
 */
export function SetDayOrNight(isday, viewer) {
	viewer.clock.currentTime = Cesium.JulianDate.fromDate(new Date())
	viewer.scene.globe.enableLighting = true;
	viewer.clock.shouldAnimate = true;
	viewer.clock.multiplier = 0;
	let jsDate = new Date(viewer.clock.currentTime.toString());
	let date = timeFormatConvert(jsDate);
	if (!isday) {
		let offset = 24 - date.H;
		viewer.clock.currentTime = Cesium.JulianDate.addHours(
			Cesium.JulianDate.now(new Date()),
			offset,
			new Cesium.JulianDate()
		);
		setAllLayerBrightnes(0.4, viewer)
	} else if (isday) {
		let offset = 12 - date.H;
		viewer.clock.currentTime = Cesium.JulianDate.addHours(
			Cesium.JulianDate.now(new Date()),
			offset,
			new Cesium.JulianDate()
		);
		setAllLayerBrightnes(1, viewer)
	}
}

//
export function setAllLayerBrightnes(brightness, viewer) {
	var layers = viewer.imageryLayers;
	for (var i = 0; i < layers.length; i++) {
		var layer = layers.get(i);
		layer.brightness = brightness;
	}
}

