import {defineStore} from "pinia";
import {GlobalState} from "../../../buss/maphandler";
import {camerasettings} from "./camera";

export const maptreesettings=defineStore('maptreesettings',{
	state:()=>{
		return {
			treedata:[
				{
					id:"1",
					label:"",
					children:[]
				}
			]
		}
	},
	actions:{
		InitTreeData(){
			if(this.treedata.length==1&&this.treedata[0].label==""&&this.treedata[0].children.length==0){
				//
				GlobalState.viewer.baseLayerPicker._viewModel.imageryProviderViewModels.forEach((item,index)=>{
					this.treedata[0].children.push({
						id:"1"+index.toString(),
						label:item.name,
						// f:item.creationFunction,
						type:0,//
					})
				})
			}
		},

	}
})


export function InitMaptreeSt(settings) {
	if (settings) {
		for (let key in settings) {
			maptreesettings()[key] = settings[key];
		}
	}
	maptreesettings().InitTreeData();
}

export function GetMaptreeSt() {
	const result = {};
	for (let key in maptreesettings().$state) {
		result[key] = maptreesettings()[key];
	}
	return result;
}

