export function InitSouceSt(settings) {
	if (settings) {
		soucesettings().title = settings.title;
		soucesettings().remark = settings.remark;
		soucesettings().dataTag = settings.dataTag;
		if(settings.thumbnailUrl!=null&&settings.thumbnailUrl!=''){
			soucesettings().setThumbnailUrl(settings.thumbnailUrl);
		}
	}
}

