function capturescreen() {
	return new Promise((resolve, reject) => {
		const viewer = GlobalState.viewer;
		const canvas = viewer.scene.canvas;

		const scalefac=0.4;

		const len=Math.min(canvas.width,canvas.height);

		const center=[(canvas.width/2),(canvas.height/2)];
		const lefttop=[center[0]-len/2,center[1]-len/2];


		//ImageDOM
		let image = new Image();
		// canvas.toDataURL Base64URL,
		//  PNG
		image.src = canvas.toDataURL("image/png");
		image.onload = function () {
			let newCanvas = document.createElement("canvas");
			const ctx = newCanvas.getContext("2d");
			newCanvas.width = len*scalefac;
			newCanvas.height = len*scalefac;
			ctx.scale(scalefac,scalefac);

			ctx.drawImage(image, lefttop[0],lefttop[1],len,len,0,0,len,len);


			newCanvas.toBlob((blod) => {
				resolve(blod);
				image.src='';
				newCanvas=null;
			}, "image/png");

		};
	});


}
