<template>
    <div id="clock">
        <el-collapse v-model="activeNames">
            <el-collapse-item title="" name="1">
                <div class="swichcontent">
                    <span>:</span>
                    <el-date-picker popper-class="popper"
                            v-model="clSt.starttime"
                            type="datetime"
                            placeholder=""
                            @change="clSt.SetStartTime()"
                    />
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-date-picker popper-class="popper"
                            v-model="clSt.endtime"
                            type="datetime"
                            placeholder=""
                            @change="clSt.SetEndTime()"
                    />
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-select popper-class="popper" placeholder="" v-model="clSt.timemode" @change="clSt.SetTimeMode()">
                        <el-option label="UNBOUND" value="1"/>
                        <el-option label="CLAMPED" value="2"/>
                        <el-option label="lOOPSTOP" value="3"/>
                    </el-select>
                </div>

            </el-collapse-item>
            <el-collapse-item title="" name="2">
                <div class="swichcontent">
                    <span>:</span>
                    <el-date-picker popper-class="popper"
                            v-model="clSt.curtime"
                            type="datetime"
                            placeholder=""
                            @change="clSt.SetCurTime()"
                    />
                </div>
                <div class="swichcontent">
                    <span>:</span>
                    <el-input-number
                            v-model="clSt.multiply"
                            :min="-10"
                            :max="10"
                            controls-position="right"
                            @change="clSt.SetMultiply()"
                    />
                </div>
            </el-collapse-item>

        </el-collapse>
    </div>
</template>

<script setup>
    import {clocksettings} from "./buss/clock";
    import {ref,onMounted} from 'vue'
    const clSt=clocksettings();

	const activeNames = ref(['1', '2']);
</script>

